	module.exports = {"data":{"markdownRemark":{"html":"<p>A data structure is a particular way of organizing data in a computer so that it can be used efficiently. Python comes with a robust set of inbuilt data structures. Some of the most commonly used ones are :-  </p>\n<ul>\n<li>Lists  </li>\n<li>Tuples  </li>\n<li>Dictionaries  </li>\n</ul>\n<p>Majorly, the data structures can be divided into two categories :-  </p>\n<ul>\n<li>Mutable :- A mutable data structure is a structure whose state can  be modified after it is created. Python Lists and Dictionaries are mutable.  </li>\n<li>Immutable :- An immutable data structure can’t be modified. Example :- Once a tuple is created, we can’t update the values inside it.  </li>\n</ul>\n<h2>Reference:</h2>\n<p><a href=\"https://docs.python.org/3.7/tutorial/datastructures.html\">Python Data Structures</a></p>","fields":{"slug":"/python/data-structures/"},"frontmatter":{"title":"The Python Data Structures"}}},"pathContext":{"slug":"/python/data-structures/"}}

